import React from "react";
import { useNavigate } from "react-router";
import Eror from "../assets/img/eror.svg";
import "./lost.css";

export default function Lost() {
  const Navigate = useNavigate();
  return (
    <div>
      <div class="main">
        <img className="eroricon" src={Eror} alt="eror" />
      </div>
      <div className="erorcon">
        <span>Error connection periksa jaringan internet</span>
        <a onClick={() => Navigate("/")} className="erortitle">
          Kembali Ke Home
        </a>
      </div>
    </div>
  );
}
